import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { API_URL, STATUSES } from '../utils/constants';
import { useNavigate } from 'react-router-dom';
import '../App.css';

function formatDate(dateStr) {
  if (!dateStr) return '';
  const d = new Date(dateStr);
  return d.toLocaleString();
}

const ComplaintList = () => {
  const [complaints, setComplaints] = useState([]);
  const [filtered, setFiltered] = useState([]);
  const [status, setStatus] = useState('');
  const [search, setSearch] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const navigate = useNavigate();

  const fetchComplaints = async (statusVal = '', searchVal = '') => {
    setError("");
    setLoading(true);
    try {
      let url = API_URL;
      if (statusVal && STATUSES.some(s => s.value === statusVal)) {
        url += '/status/' + statusVal;
      }
      const resp = await axios.get(url);
      let items = resp.data || [];
      if (searchVal) {
        items = items.filter(item => item.title.toLowerCase().includes(searchVal.toLowerCase()));
      }
      setComplaints(items);
      setFiltered(items);
    } catch (err) {
      setError('Failed to load complaints.');
      setComplaints([]);
      setFiltered([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchComplaints(status, search);
    // eslint-disable-next-line
  }, [status]);

  useEffect(() => {
    let filteredList = complaints;
    if (search) {
      filteredList = complaints.filter(item => item.title.toLowerCase().includes(search.toLowerCase()));
    }
    setFiltered(filteredList);
    // eslint-disable-next-line
  }, [search, complaints]);

  const handleStatusChange = (e) => setStatus(e.target.value);
  const handleSearch = (e) => setSearch(e.target.value);

  return (
    <div className="form-container" style={{ maxWidth: 600 }}>
      <h2>All Complaints</h2>
      <div style={{display:'flex',gap:'1rem',alignItems:'center',marginBottom:'1rem'}}>
        <label htmlFor="statusFilter" style={{margin:'0'}}>
          Status:
        </label>
        <select id="statusFilter" data-testid="status-filter" name="status" value={status} onChange={handleStatusChange} style={{width:'150px'}}>
          <option value="">All</option>
          {STATUSES.map(s => <option key={s.value} value={s.value}>{s.label}</option>)}
        </select>
        <input
          id="searchBox"
          data-testid="search-input"
          type="text"
          value={search}
          placeholder="Search by title..."
          onChange={handleSearch}
          style={{flex:1,maxWidth:'260px',marginLeft:'2rem'}}
        />
      </div>
      {loading ? (
        <div className="empty-state">Loading complaints...</div>
      ) : error ? (
        <div className="error">{error}</div>
      ) : filtered.length === 0 ? (
        <div className="empty-state">No complaints found.</div>
      ) : (
        <ul style={{ padding: 0, listStyle: 'none' }}>
          {filtered.map(c => (
            <li key={c.id} style={{
              border: '1px solid var(--border-light)',
              borderRadius: '8px',
              padding: '1rem',
              marginBottom: '.6rem',
              display: 'flex',
              alignItems: 'flex-start',
              background: '#fff',
              cursor: 'pointer',
            }} onClick={() => navigate(`/complaints/${c.id}`)} data-testid={`complaint-item-${c.id}`}>
              <div style={{ flex: 1, maxWidth: '70%' }}>
                <span style={{fontWeight:600,overflowWrap:'break-word',display:'block',fontSize:'1.1rem'}}>
                  {c.title}
                </span>
                <div style={{fontSize:'.95rem',color:'var(--text-muted)'}}>{formatDate(c.submittedDate)}</div>
              </div>
              <div style={{minWidth:'100px',textAlign:'right'}}>
                <div style={{fontWeight:600,textTransform:'capitalize'}}>{c.status.replace('_',' ').toLowerCase()}</div>
                <div style={{fontSize:'.95rem',color:'var(--text-secondary)'}}>Priority: {c.priority}</div>
              </div>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default ComplaintList;
