import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { API_URL } from '../utils/constants';
import { useParams, useNavigate } from 'react-router-dom';
import '../App.css';

function formatDate(dateStr) {
  if (!dateStr) return '';
  const d = new Date(dateStr);
  return d.toLocaleString();
}

const ComplaintDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [complaint, setComplaint] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    setLoading(true);
    setError("");
    axios.get(`${API_URL}/${id}`)
      .then(res => {
        setComplaint(res.data);
        setError("");
      })
      .catch(err => {
        setError(err?.response?.data?.message || 'Failed to load complaint.');
      })
      .finally(() => setLoading(false));
  }, [id]);

  if (loading) return <div className="form-container"><div className="empty-state">Loading...</div></div>;

  if (error) return <div className="form-container"><div className="error">{error}</div><button className="btn-secondary" onClick={() => navigate('/complaints')}>Back to List</button></div>;

  if (!complaint) return null;

  return (
    <div className="form-container" style={{ maxWidth: 600 }}>
      <h2>Complaint Details</h2>
      <div style={{ marginBottom: '1.5rem' }}>
        <div><b>Title:</b> {complaint.title}</div>
        <div style={{ margin: '0.5rem 0' }}><b>Description:</b> {complaint.description}</div>
        <div><b>Category:</b> {complaint.category}</div>
        <div><b>Status:</b> {complaint.status.replace('_',' ').toLowerCase()}</div>
        <div><b>Priority:</b> {complaint.priority}</div>
        <div><b>Submitted By:</b> {complaint.submittedBy}</div>
        <div><b>Submitter Email:</b> {complaint.submitterEmail}</div>
        <div><b>Submitted Date:</b> {formatDate(complaint.submittedDate)}</div>
        {complaint.status === 'RESOLVED' && (
          <>
            <div><b>Resolved Date:</b> {formatDate(complaint.resolvedDate)}</div>
            {complaint.resolutionComments && <div><b>Resolution Comments:</b> {complaint.resolutionComments}</div>}
          </>
        )}
        {complaint.assignedTo && <div><b>Assigned To:</b> {complaint.assignedTo}</div>}
      </div>
      <button onClick={() => navigate('/complaints')} className="btn-secondary">Back to List</button>
    </div>
  );
};

export default ComplaintDetail;
