import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import ComplaintForm from './components/ComplaintForm';
import ComplaintList from './components/ComplaintList';
import ComplaintDetail from './components/ComplaintDetail';
import './App.css';

function App() {
  return (
    <Router>
      <nav style={{display:'flex',gap:'1.5rem',padding:'1rem 2rem',borderBottom:'1px solid #e5e7eb',justifyContent:'center',background:'#f6faff'}}>
        <Link to="/complaints/new" style={{textDecoration:'none',color:'var(--accent)',fontWeight:'600'}}>Submit Complaint</Link>
        <Link to="/complaints" style={{textDecoration:'none',color:'var(--accent)',fontWeight:'600'}}>View Complaints</Link>
      </nav>
      <Routes>
        <Route path="/" element={<div className="form-container" style={{textAlign:'center'}}><h2>Complaint Management Portal</h2><p>Welcome! Use the links above to submit or view complaints.</p></div>} />
        <Route path="/complaints/new" element={<ComplaintForm />} />
        <Route path="/complaints" element={<ComplaintList />} />
        <Route path="/complaints/:id" element={<ComplaintDetail />} />
      </Routes>
    </Router>
  );
}
export default App;
