import React, { useState } from 'react';
import axios from 'axios';
import { API_URL, CATEGORIES } from '../utils/constants';
import '../App.css';

const ComplaintForm = () => {
  const [form, setForm] = useState({
    title: '',
    description: '',
    category: '',
    submittedBy: '',
    submitterEmail: '',
  });
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState("");
  const [apiError, setApiError] = useState("");

  const validate = () => {
    let err = {};
    if (!form.title || form.title.trim().length < 5 || form.title.trim().length > 100) {
      err.title = 'Title must be between 5 and 100 characters';
    }
    if (!form.description || form.description.trim().length < 10 || form.description.trim().length > 500) {
      err.description = 'Description must be between 10 and 500 characters';
    }
    if (!form.category || !CATEGORIES.some(cat => cat.value === form.category)) {
      err.category = 'Category is required';
    }
    if (!form.submittedBy || form.submittedBy.trim().length < 2 || form.submittedBy.trim().length > 50) {
      err.submittedBy = 'Name must be between 2 and 50 characters';
    }
    if (!form.submitterEmail || !/^[^@\s]+@[^@\s]+\.[^@\s]+$/.test(form.submitterEmail)) {
      err.submitterEmail = 'Email must be valid';
    }
    return err;
  };

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
    setErrors({ ...errors, [e.target.name]: undefined });
    setApiError("");
    setSuccess("");
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSuccess("");
    setApiError("");
    const err = validate();
    setErrors(err);
    if (Object.keys(err).length !== 0) {
      return;
    }
    setLoading(true);
    try {
      await axios.post(API_URL, form);
      setSuccess("Complaint submitted successfully!");
      setForm({ title: '', description: '', category: '', submittedBy: '', submitterEmail: '' });
    } catch (error) {
      setApiError(
        error?.response?.data?.message || 'Submission failed. Try again.'
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="form-container">
      <h2>Submit a Complaint</h2>
      <form onSubmit={handleSubmit} data-testid="complaint-form">
        {/* Title */}
        <label htmlFor="title">Title</label>
        <input
          id="title"
          name="title"
          type="text"
          value={form.title}
          onChange={handleChange}
          disabled={loading}
          aria-label="Title"
        />
        {errors.title && <div className="error">{errors.title}</div>}

        {/* Description */}
        <label htmlFor="description">Description</label>
        <textarea
          id="description"
          name="description"
          value={form.description}
          onChange={handleChange}
          disabled={loading}
          aria-label="Description"
        />
        {errors.description && <div className="error">{errors.description}</div>}

        {/* Category */}
        <label htmlFor="category">Category</label>
        <select
          id="category"
          name="category"
          value={form.category}
          onChange={handleChange}
          disabled={loading}
          aria-label="Category"
        >
          <option value="">Select Category</option>
          {CATEGORIES.map((cat) => (
            <option key={cat.value} value={cat.value}>{cat.label}</option>
          ))}
        </select>
        {errors.category && <div className="error">{errors.category}</div>}

        {/* Name */}
        <label htmlFor="submittedBy">Name</label>
        <input
          id="submittedBy"
          name="submittedBy"
          type="text"
          value={form.submittedBy}
          onChange={handleChange}
          disabled={loading}
          aria-label="Name"
        />
        {errors.submittedBy && <div className="error">{errors.submittedBy}</div>}

        {/* Email */}
        <label htmlFor="submitterEmail">Email</label>
        <input
          id="submitterEmail"
          name="submitterEmail"
          type="email"
          value={form.submitterEmail}
          onChange={handleChange}
          disabled={loading}
          aria-label="Email"
        />
        {errors.submitterEmail && <div className="error">{errors.submitterEmail}</div>}

        <button className="btn-primary" type="submit" disabled={loading}>{loading ? 'Submitting...' : 'Submit'}</button>
      </form>
      {success && <div style={{ color: 'green', marginTop: '1rem' }}>{success}</div>}
      {apiError && <div className="error">{apiError}</div>}
    </div>
  );
};

export default ComplaintForm;
